.section-register {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.register {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: 0 auto;
  height: 100%;
  padding: 0 15px;
  width: 100%;
}

.register__header {
  display: flex;
  flex-direction: column;
  padding-top: 70px;
}

.register__title {
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.33;
  margin: 0;
  padding: 35px 0 40px 0;
}

.register__main {
  display: flex;
  flex-direction: column;
  gap: 22px;
  margin-bottom: auto;
}

.register__inner {
  display: flex;
  flex-direction: column;
}

.register__inner-text {
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 10px;
  font-weight: 400;
  color: #A0A0A0;
  padding-bottom: 5px;
}

.register__input {
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.33;
  height: 25px;
  border: 0;
  background-color: rgb(255, 255, 255);
  border-bottom: 1px solid #E8E8E8;
}

.register__input::placeholder {
  color: #CCCCCC;
}

.register__input:focus {
  outline: none;
  border-bottom: 1px solid #3456F3;
}

.register__container {
  margin-bottom: 71px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
}

.register__text-error {
  display: block;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 11px;
  font-weight: 400;
  color: #FF004C;
  text-align: center;
}

.register__submit {
  width: 100%;
  height: 45px;
  background-color: #3456F3;
  color: #fff;
  border: navajowhite;
  border-radius: 3px;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  opacity: 1;
  transition: opacity .2s linear;
}

.register__submit:hover {
  opacity: .7;
}

.register__wrap {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.register__wrap-text {
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #A0A0A0;
}

.register__wrap-link {
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #3456F3;
  text-decoration: none;
  opacity: 1;
  transition: opacity .2s linear;
}

.register__wrap-link:hover {
  opacity: .7;
}

.section-register_background-activ {
  background-color: rgba(0, 0, 0, .3);
  ;
  opacity: .5;
}

@media screen and (max-width: 768px) {
  .register {
    margin: 0 auto;
    height: 70vh;
  }

  .register__header {
    padding-top: 77px;
  }

  .register__container {
    margin-bottom: 81px;
  }

}

@media screen and (max-width: 470px) {
  .register {
    height: 100vh;
    padding: 0 30px;
  }

  .register__header {
    align-items: center;
    padding-top: 55px;
  }

  .register__title {

    padding-top: 47px;
  }

  .register__main {
    padding-top: 37px;
    gap:30px;
  }
  .register__inner-text {
    padding-bottom: 0px;
}

  .register__container {
margin-bottom: 30px;
  }

  .register__submit {
    font-size: 12px;
  }

  .register__wrap-text {
    font-size: 12px;
  }

  .register__wrap-link {
    font-size: 12px;
  }
}
